@import '../../../asset/style/variable';

.dropdownToggle {
  border: none;
  font-size: $font-size-lg;
  font-weight: $font-weight-lg;
  color: $snow;
  background: transparent;
  opacity: $opacity-blur;
  &:hover,
  &:focus,
  &:focus-visible {
    box-shadow: none;
    border: none;
    color: $snow;
    background: transparent;
    box-shadow: none;
    outline: none;
    opacity: $opacity-default;
  }
}

.dropdownMenu {
  border-radius: $border-radius;
  font-size: $font-size-lg;
  font-weight: $font-weight-lg;
  background-color: $raven;
}

.logo {
  width: 24px;
  height: 24px;
  margin-right: 6px;
}

.title {
  margin-top: 2px;
}
